import React, { useEffect, useState } from 'react'

export default function Product({ prodName, prodImage, prodDescription, prodSku }) {
    const [isValidImage, setIsValidImage] = useState(true);

    useEffect(() => {
        if (!prodImage) {
            setIsValidImage(false);
            return;
        }

        const img = new Image();
        img.onload = () => setIsValidImage(true);
        img.onerror = () => setIsValidImage(false);
        img.src = prodImage;
    }, [prodImage]);

  return (
    <div className="col-md-4 mb-4">
      <div className="card product-card h-100">
        {prodImage && (
          <div className="product-image">
            {
                isValidImage ? 
                (
                    <img 
                        src={prodImage}
                        alt={prodName}
                    />
                ) : (
                    <img src="https://placehold.co/414x220" alt="No image available" />
                )
            }
          </div>
        )}
        <div className="card-body">
          <h5 className="product-name">{prodName}</h5>
          {prodSku && <small className="text-muted d-block mb-2">SKU: {prodSku}</small>}
          <p className="card-text" dangerouslySetInnerHTML={{ __html: prodDescription }}></p>
        </div>
      </div>
    </div>
  );
}